in-height: 44px;\r\n  white-space: nowrap;\r\n  transition: all 0.2s ease-in-out;\r\n}\r\n\r\n.nav-item a:hover {\r\n  text-decoration: underline;\r\n  color: #f9f8f8;\r\n}\r\n\r\n/* jumbotron */\r\n\r\n.hero {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  min-height: 400px;\r\n  width: 100%;\r\n  background-image: linear-gradient(rgba(0, 0, 5, 0.5), rgba(0, 0, 5, 0.5)),\r\n    -webkit-image-set(\r\n      url(\"../public/images/heros/hero-image_2-small.jpg\") 1x,\r\n      url(\"../public/images/heros/hero-image_2-large-large-large.jpg\") 2x\r\n    );\r\n  background-image: image-set(\r\n    url(\"../public/images/heros/hero-image_2-small.jpg\") 1x,\r\n    url(\"../public/images/heros/hero-image_2-large-large-large.jpg\") 2x\r\n  );\r\n  background-position: center;\r\n  background-size: cover;\r\n}\r\n\r\n\r\n.hero-inner {\r\n  text-align: center;\r\n  color: #ffffff;\r\n  font-weight: 500;\r\n  font-size: 18px;\r\n}\r\n\r\n.hero-tagline {\r\n  color: #fff;\r\n  margin-top: 16px;\r\n  font-size: 18px;\r\n  font-weight: 300;\r\n  text-align: center;\r\n}\r\n\r\n/* main */\r\n\r\n.content h1 {\r\n  text-align: center;\r\n  margin-top: 30px;\r\n  font-size: 30px;\r\n}\r\n.content {\r\n  margin: 0 auto;\r\n  min-height: 100%;\r\n}\r\n\r\n.content .content__heading {\r\n  font-weight: normal;\r\n}\r\n\r\n/* card */\r\n\r\n.card-container {\r\n  display: flex;\r\n  justify-content: center;\r\n  flex-wrap: wrap;\r\n  margin-top: 10px;\r\n}\r\n\r\n.card {\r\n  width: 280px;\r\n  background-color: #d5d3c7;\r\n  border-radius: 8px;\r\n  overflow: hidden;\r\n  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);\r\n  margin: 20px;\r\n}\r\n\r\n.card img {\r\n  width: 100%;\r\n  max-height: 200px;\r\n}\r\n\r\n.card-content {\r\n  padding: 16px;\r\n}\r\n\r\n.card-content h2 a {\r\n  padding: 10px 0 10px 0;\r\n  font-size: 28px;\r\n  margin-bottom: 8px;\r\n  text-decoration: none;\r\n  color: black;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n}\r\n\r\n.card-content p {\r\n  font-size: 15px;\r\n  line-height: 1.3;\r\n  padding-top: 8px;\r\n}\r\n\r\n.card-content .rating {\r\n  margin-top: 10px;\r\n}\r\n\r\n.card-content .card-rating {\r\n  text-decoration: none;\r\n  padding: 3px;\r\n  font-size: 15px;\r\n  font-weight: 500;\r\n  color: black;\r\n}\r\n\r\n.location {\r\n  position: absolute;\r\n  background-color: rgba(0, 0, 0, 0.7);\r\n  padding: 10px;\r\n  display: flex;\r\n  justify-content: space-between;\r\n}\r\n\r\n.location .city {\r\n  color: white;\r\n  text-decoration: none;\r\n  font-weight: 500;\r\n  padding-left: 6px;\r\n}\r\n\r\n.location .fa-location-dot {\r\n  color: white;\r\n  font-size: 16px;\r\n}\r\n\r\n/* detail */\r\n.detail-page {\r\n  padding: 10px;\r\n}\r\n.detail-resto {\r\n  margin-top: 30px;\r\n  padding: 40px 20px;\r\n  width: 100%;\r\n}\r\n\r\n.detail {\r\n  display: flex;\r\n  flex-direction: column;\r\n  width: 100%;\r\n  margin-bottom: 30px;\r\n  padding: 10px;\r\n  max-width: 100%;\r\n  margin: auto;\r\n  margin-top: 3%;\r\n  border-radius: 8px;\r\n  background-color: white;\r\n  box-shadow: 5px 5px 10px 3px rgba(0, 0, 0, 0.1);\r\n}\r\n.main_img img {\r\n  width: 100%;\r\n  height: 100%;\r\n  border-radius: 10px;\r\n  position: relative;\r\n}\r\n\r\n.name {\r\n  margin-top: 10px;\r\n  font-size: 40px;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.right span {\r\n  font-size: 23px;\r\n  font-weight: 600;\r\n}\r\n.right p {\r\n  font-size: 18px;\r\n  padding-block: 10px;\r\n  font-weight: 400;\r\n}\r\n.des p {\r\n  font-size: 18px;\r\n  padding-block: 10px;\r\n  font-weight: 400;\r\n}\r\n.des span {\r\n  font-size: 23px;\r\n  font-weight: 600;\r\n}\r\nhr {\r\n  margin-top: 50px;\r\n}\r\n\r\n/* review */\r\n.review {\r\n  width: 100%;\r\n  margin-block: 30px;\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n}\r\n\r\n.review-title h2 {\r\n  font-size: 24px;\r\n  font-weight: 600;\r\n  text-align: center;\r\n  margin-top: 10px;\r\n  margin-bottom: 10px;\r\n}\r\n.card-review {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  text-align: center;\r\n}\r\n.review-content {\r\n  margin-block: 15px;\r\n  border-radius: 10px;\r\n  background-color: white rgba(128, 128, 128, 0.195);\r\n  box-shadow: 5px 5px 10px 3px rgba(0, 0, 0, 0.1);\r\n  padding: 10px;\r\n}\r\n\r\n.review-content i {\r\n  font-size: 40px;\r\n}\r\n\r\n.review-inner h3 {\r\n  font-size: 20px;\r\n  line-height: 28px;\r\n  font-weight: 500;\r\n  padding-top: 3px;\r\n}\r\n.review-inner p {\r\n  font-size: 18px;\r\n  color: rgb(77, 73, 73);\r\n  padding-top: 3px;\r\n}\r\n\r\n/* like */\r\n.like {\r\n  width: 55px;\r\n  height: 55px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background-color: #db0000;\r\n\r\n  position: fixed;\r\n  bottom: 16px;\r\n  right: 16px;\r\n  border-radius: 50%;\r\n  border: 0;\r\n\r\n  font-size: 18px;\r\n  color: white;\r\n  cursor: pointer;\r\n}\r\n\r\n/* add review */\r\n.input_section {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  padding: 8%;\r\n}\r\n \r\n.input_section .form_review {\r\n  border: 1px solid black;\r\n  border-radius: 10px;\r\n  background-color: white;\r\n  box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.2);\r\n  padding: 20px;\r\n  width: 60%;\r\n}\r\n \r\n.input_section h2 {\r\n  text-align: center;\r\n  color: black;\r\n  margin-bottom: 15px;\r\n  margin-top: 5px;\r\n  font-size: 30px;\r\n}\r\n \r\n.input_section form .input {\r\n  margin: 18px 0;\r\n}\r\n \r\n.input_section form button {\r\n  background-color: #8b0000;\r\n  color: white;\r\n  border: 0;\r\n  font-size: 18px;\r\n  font-weight: 600;\r\n  border-radius: 5px;\r\n  display: block;\r\n  width: 100%;\r\n  padding: 15px;\r\n  cursor: pointer;\r\n}\r\n \r\n.input_section form .input input {\r\n  display: block;\r\n  width: 100%;\r\n  height: 40px;\r\n  padding: 22px;\r\n  border-radius: 5px;\r\n  margin-top: 10px;\r\n}\r\n \r\n.input_section form .input textarea {\r\n  display: block;\r\n  width: 100%;\r\n  height: 100px;\r\n  padding: 20px;\r\n  border-radius: 5px;\r\n  margin-top: 10px;\r\n}\r\n.input_section form .input label {\r\n  color: black;\r\n  font-weight: bold;\r\n  margin-left: 5px;\r\n  font-size: 17px;\r\n}\r\n/* favorite */\r\n.content-favorite > h2 {\r\n  text-align: center;\r\n  margin-top: 100px;\r\n  font-size: 30px;\r\n}\r\n.content-favorite {\r\n  margin: 0 auto;\r\n  min-height: 100%;\r\n}\r\n.card-favorite {\r\n  display: flex;\r\n  justify-content: center;\r\n  flex-wrap: wrap;\r\n  margin-top: 10px;\r\n}\r\n\r\n/* preloader */\r\n#containerLoading {\r\n  display: flex;\r\n  position: fixed;\r\n  justify-content: center;\r\n  align-items: center;\r\n  top: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  left: 0;\r\n  z-index: 9999;\r\n  background-color: rgb(248, 246, 246);\r\n}\r\n \r\n.preloader {\r\n  width: 150px;\r\n  height: 150px;\r\n  border: solid 20px #ccc;\r\n  border-top-color: #8b0000;\r\n  border-radius: 100%;\r\n  margin: auto;\r\n  animation: loading 2s linear infinite;\r\n}\r\n \r\n.loader-hidden {\r\n  visibility: hidden;\r\n}\r\n \r\n@keyframes loading {\r\n  from {\r\n    transform: rotate(0deg);\r\n  }\r\n  to {\r\n    transform: rotate(360deg);\r\n  }\r\n}\r\n\r\n/* footer */\r\nfooter {\r\n  padding: 16px;\r\n}\r\n\r\nfooter p {\r\n  text-align: center;\r\n  color: #8b0000;\r\n  font-weight: 700;\r\n}\r\n"],"sourceRoot":""}]);
